import './index.scss';

import React, {useEffect, useState, useRef} from 'react';
import noImage from '../../../assets/static/images/no-image.png'
import mmd from "../../../assets/mmd";

const ProjectItem = ({
  title,
  term,
  imgUrl = '../../../assets/static/images/no-image.png',
  tags,
  description,
  attribution,
  projectUrl,
  githubUrl,
  explain
}) => {
  const [projectExplainText, setProjectExplainText] = useState('');
  useEffect(() => {
    if(explain) {
      import(`../../../assets/static/markdown/${explain}`)
      .then((res) => {
        fetch(res.default)
        .then((res) => res.text())
        .then((res) => setProjectExplainText(res))
        .catch((err) => console.log(err));
      })
      .catch((err) => {
        console.log(err);
        return '';
      });
    } else {
      return '';
    }
  });

  const explainRef = useRef(null);
  if(explainRef.current) {
    explainRef.current.innerHTML = mmd(projectExplainText);
  }

  const addDefaultSrc=({target}) => (target.src = noImage)

  return (
    <div className='project'>
      <div className='project__contents'>
        <div className='project__details'>
          <div className='item__image'>
            <img src={imgUrl} alt={`${title} logo ì´ë¯¸ì§€`} onError={addDefaultSrc} />
          </div>
          <div>
            <a className='project__item_wrapper' href={projectUrl} target='_blank' rel='noopener noreferrer'>
              <div className='item__details'>
                <div className='title'>
                  {title} <span className='term'>({term})</span>
                </div>
                <div className='description'>{description}</div>
                <div className='attribution'>{attribution}</div>
                <div className='tags'>
                  {tags.map((tag, index) => (
                    <div key={index} className='tag'>
                      {tag}
                    </div>
                  ))}
                </div>
              </div>

              {githubUrl && (
                <div className='project__links'>
                  <a href={githubUrl}>
                    <div className='github-logo' />
                  </a>
                </div>
              )}
            </a>
            {explain && (
              <div className='project__explain' ref={explainRef}></div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};
export default ProjectItem;
